/**
 * Transcript Refinement Module
 * Refines YouTube transcripts using LLM with parallel batch processing
 */

import { HumanMessage, SystemMessage } from "@langchain/core/messages";
import { ChatOpenAI } from "@langchain/openai";

const REFINER_CONFIG = {
  MODEL: "google/gemini-2.5-flash-lite",
  MAX_SEGMENTS_PER_CHUNK: 50,
  CHUNK_SENTINEL: "<<<__CHUNK_END__>>>",
};

/**
 * Chunk segments into groups of at most max_segments_per_chunk segments.
 * Returns list of [startIdx, endIdx] ranges where endIdx is exclusive.
 */
function chunkSegmentsByCount(segments, maxSegmentsPerChunk) {
  const ranges = [];
  const n = segments.length;
  let start = 0;
  while (start < n) {
    const end = Math.min(start + maxSegmentsPerChunk, n);
    ranges.push([start, end]);
    start = end;
  }
  return ranges;
}

/**
 * Simple parser: Align refined lines to original segments by index, preserving timestamps.
 * Assumes line counts match; falls back to original text on mismatch.
 */
function parseRefinedSegments(refinedText, originalSegments, sentinel, maxPerChunk) {
  if (!refinedText || !originalSegments.length) return originalSegments;

  // Split by sentinel to get chunk blocks
  const rawBlocks = refinedText.split(sentinel);

  // Compute original chunk ranges
  const ranges = chunkSegmentsByCount(originalSegments, maxPerChunk);

  // Pad blocks if fewer than ranges
  while (rawBlocks.length < ranges.length) {
    rawBlocks.push("");
  }

  const refinedSegments = [];
  for (let i = 0; i < ranges.length; i++) {
    const [startIdx, endIdx] = ranges[i];
    const origChunk = originalSegments.slice(startIdx, endIdx);
    const blockText = rawBlocks[i] || "";
    const refinedLines = blockText.trim().split('\n').filter(line => line.trim());

    // Align by index, fallback to original
    for (let j = 0; j < origChunk.length; j++) {
      const origSeg = origChunk[j];
      const refinedText = j < refinedLines.length ? refinedLines[j].trim() : origSeg.text;
      refinedSegments.push({
        text: refinedText || origSeg.text,
        startMs: origSeg.startMs,
        endMs: origSeg.endMs,
        startTimeText: origSeg.startTimeText,
      });
    }
  }

  return refinedSegments;
}

/**
 * Refine transcript using LangChain JS with OpenRouter batch processing
 * 
 * @param {Array<Object>} transcript - Original transcript segments
 * @param {string} title - Video title
 * @param {string} description - Video description
 * @param {string} apiKey - OpenRouter API key
 * @param {Function} onProgress - Progress callback (chunkIdx, totalChunks)
 * @returns {Promise<Array<Object>>} Refined segments with preserved timestamps
 */
async function refineTranscriptWithLLM(transcript, title, description, apiKey, onProgress = null) {
  if (!transcript?.length) {
    throw new Error("No transcript segments provided");
  }

  const { MODEL, MAX_SEGMENTS_PER_CHUNK, CHUNK_SENTINEL } = REFINER_CONFIG;

  const systemPrompt = `You are correcting segments of a YouTube video transcript. These segments could be from anywhere in the video (beginning, middle, or end). Use the video title and description for context.

CRITICAL CONSTRAINTS:
- Only fix typos and grammar. Do NOT change meaning or structure.
- PRESERVE ALL NEWLINES: each line is a distinct transcript segment.
- Do NOT add, remove, or merge lines. Keep the same number of lines.
- MAINTAIN SIMILAR LINE LENGTHS: Each output line should be approximately the same character count as its corresponding input line (±10% tolerance). Do NOT expand short lines into long paragraphs. Do NOT condense long lines significantly. Keep each line concise.
- If a sentence is broken across lines, keep it broken the same way.
- PRESERVE THE ORIGINAL LANGUAGE: output must be in the same language as the input transcript.
- Focus on minimal corrections: fix typos, correct grammar errors, but keep expansions/additions to an absolute minimum.

EXAMPLES OF CORRECT BEHAVIOR:

Input:
up to 900. From 900 up to 1,100.
If you sold at the reasonable
valuations, when the gains that already
been had, you missed out big time. I

Output:
up to $900. From $900 up to $1,100.
If you sold at the reasonable
valuations, when the gains that already
had been had, you missed out big time. I`;

  const userPreamble = `Video Title: ${title || ''}
Video Description: ${description || ''}

Transcript Chunk:`;

  // Chunking
  const ranges = chunkSegmentsByCount(transcript, MAX_SEGMENTS_PER_CHUNK);
  const startTime = Date.now();
  console.log(`\n${'='.repeat(80)}`);
  console.log("CHUNK ARRANGEMENT");
  console.log(`${'='.repeat(80)}`);
  console.log(`Total segments: ${transcript.length}`);
  console.log(`Total chunks: ${ranges.length}`);
  console.log("\nChunk details:");
  ranges.forEach(([startIdx, endIdx], chunkIdx) => {
    const numSegments = endIdx - startIdx;
    console.log(`  Chunk ${chunkIdx + 1}: segments ${startIdx}-${endIdx - 1} (${numSegments} segments)`);
  });
  console.log(`${'='.repeat(80)}\n`);

  console.log(`Processing ${ranges.length} chunks in parallel...`);

  // Create llm instance
  const llm = new ChatOpenAI({
    model: MODEL,
    apiKey: apiKey,
    configuration: {
      baseURL: "https://openrouter.ai/api/v1",
      defaultHeaders: {
        "HTTP-Referer": typeof chrome !== "undefined" && chrome.runtime ? chrome.runtime.getURL('') : "https://github.com/better-youtube-caption",
        "X-Title": "Better YouTube Caption",
      },
    },
    temperature: 0,
    useResponsesApi: true,
    reasoning: { effort: 'minimal' },
    extraBody: {
      include_reasoning: false,
      provider: { sort: 'throughput' },
    },
  });

  // Prepare batch messages
  const batchMessages = [];
  const chunkInfo = [];
  for (let chunkIdx = 0; chunkIdx < ranges.length; chunkIdx++) {
    const [startIdx, endIdx] = ranges[chunkIdx];
    const chunkSegments = transcript.slice(startIdx, endIdx);
    const expectedLineCount = chunkSegments.length;
    const chunkTextOnly = chunkSegments.map(seg => (seg.text || '').split(/\s+/).join(' ')).join('\n');

    const messages = [
      new SystemMessage(systemPrompt),
      new HumanMessage(`${userPreamble}\n${chunkTextOnly}`),
    ];

    batchMessages.push(messages);
    chunkInfo.push({
      chunkIdx: chunkIdx + 1,
      startIdx,
      endIdx,
      expectedLineCount,
    });
  }

  let responses;
  try {
    responses = await llm.batch(batchMessages);
  } catch (error) {
    console.error("Batch processing failed:", error);
    throw new Error(`LLM batch refinement failed: ${error.message}`);
  }

  // Process responses
  const allRefinedLines = [];
  for (let i = 0; i < responses.length; i++) {
    const response = responses[i];
    const info = chunkInfo[i];
    const chunkIdx = info.chunkIdx;
    const expectedLineCount = info.expectedLineCount;

    // Extract refined text (adjust for OpenRouter/LangChain JS)
    let refinedText = response.content;
    if (response.content_blocks && response.content_blocks.length > 0) {
      refinedText = response.content_blocks[0].text || refinedText;
    }
    const refinedLines = refinedText.trim().split('\n');
    const actualLineCount = refinedLines.length;

    console.log(`  Chunk ${chunkIdx} completed: received ${actualLineCount} lines (expected ${expectedLineCount})`);
    if (actualLineCount !== expectedLineCount) {
      console.warn(`  ⚠️  WARNING: Line count mismatch in chunk ${chunkIdx}!`);
    }

    allRefinedLines.push(...refinedLines);
    allRefinedLines.push(CHUNK_SENTINEL);  // Insert sentinel

    if (onProgress) onProgress(chunkIdx, ranges.length);
  }

  console.log(`All chunks completed in ${((Date.now() - startTime) / 1000).toFixed(2)}s`);

  // Build refined text and parse back to segments
  const refinedText = allRefinedLines.join('\n');
  const refinedSegments = parseRefinedSegments(refinedText, transcript, CHUNK_SENTINEL, MAX_SEGMENTS_PER_CHUNK);

  console.log(`Refinement complete: ${refinedSegments.length} segments`);
  return refinedSegments;
}

// Export for Node.js and browser
if (typeof globalThis !== 'undefined') {
  globalThis.refineTranscriptWithLLM = refineTranscriptWithLLM;
  globalThis.REFINER_CONFIG = REFINER_CONFIG;
}

export { REFINER_CONFIG, refineTranscriptWithLLM };

